import moment from 'moment'
export default {
    data() {
        return {
            modal6: false,//
            loading: true,
        }
    },
    methods: {
        show(index) {
            this.modal6 = true
            Object.assign(this.$data.formValidate, this.$options.data().formValidate)
            Object.assign(this.$data.formValidate, this.fliter.data6[index])//
        },
        asyncOK() {
            this.modal6 = false;
        },
        getData() {
            this.$http.post(this.getTest() + `/${this.apimodel}/list`, this.fliter).then(res => {
                let ii = res.data.rows.length
                for (let i = 0; i < ii; i++) {
                    res.data.rows[i].date = moment(res.data.rows[i].date).format("YYYYMMDDHHmmss")//moment
                }
                this.fliter.data6 = res.data.rows//
                this.fliter.total = res.data.total
            })
        },
        changePage(page) {
            this.fliter.page = page//page
            this.getData()
        },
        search() {
            this.getData()
        },
        remove(index) {
            this.$Modal.confirm({
                title: '',
                content: '',
                loading: true,
                onOk: () => {
                    setTimeout(() => {
                        this.$http.delete(this.getTest() + `/${this.apimodel}/data/` + this.fliter.data6[index]._id).then(res => {
                            //id
                            this.fliter.data6.splice(index, 1);
                            this.getData()
                        })
                        this.$Modal.remove();
                    }, 500);
                }
            })
        },
        onAdd() {
            this.modal6 = true,//
                Object.assign(this.$data.formValidate, this.$options.data().formValidate)//
        },
        handleSubmit() {
            this.formValidate.type = this.type
            this.formValidate.cateId = this.cateId
            this.formValidate.date = new Date()
            //cateID,type
            this.$http.post( this.getTest() + `/${this.apimodel}/data`, this.formValidate).then(res => {
                this.getData()
                this.modal6 = false;
                this.formValidate.type = "";
            })
        }
    }
}


