<template>
	<div class="content">
		<Input v-model="fliter.title">
		<Button slot="append" icon="ios-search" @click="search"></Button>
		<Button type="success" slot="append" style="width: 80px;margin-left: 10px;background: lightgreen;color: white;" @click="onAdd"></Button>
		</Input>
		
		<div class="content-body">
			<Table border :columns="columns7" :data="fliter.data6"></Table>
		</div>
		<div class="content-foot">
			<Page :total="fliter.total" show-elevator @on-change="changePage"></Page>
		</div>
		<Modal v-model="modal6" title="" :loading="loading" @on-ok="asyncOK">
			<Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
				<FormItem label="" prop="title">
					<Input v-model="formValidate.title" placeholder="Enter your title"></Input>
				</FormItem>
				<FormItem label="" prop="title">
					<Input v-model="formValidate.num" placeholder="Enter your num"></Input>
				</FormItem>
				<FormItem label="" prop="title">
					<Input v-model="formValidate.education" placeholder="Enter your education"></Input>
				</FormItem>
				<FormItem label="" prop="title">
					<Input v-model="formValidate.age" placeholder="Enter your age"></Input>
				</FormItem>
				<FormItem label="" prop="title">
					<Input v-model="formValidate.pay" placeholder="Enter your pay"></Input>
				</FormItem>
				<FormItem label="" prop="title">
					<Input v-model="formValidate.language" placeholder="Enter your language"></Input>
				</FormItem>
				<FormItem label="" prop="title">
					<Input v-model="formValidate.sex" placeholder="Enter your sex"></Input>
				</FormItem>
				<FormItem label="" prop="telephone">
					<Input v-model="formValidate.telephone" placeholder="Enter your telephone"></Input>
				</FormItem>
				<FormItem label="" prop="city">
					<Select v-model="formValidate.site" placeholder="Select your site">
						<Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
					</Select>
				</FormItem>
				<FormItem label="" prop="detail">
					<Input v-model="formValidate.detail" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="Enter something..."></Input>
				</FormItem>
				<FormItem>
					<div v-if="formValidate.type"><!--type-->
					<Button type="primary" @click="handleUpdate('formValidate')"></Button>
					</div>
					<div v-else>
					<Button type="primary" @click="handleSubmit1('formValidate')"></Button>
					</div>
				</FormItem>
			</Form>
		</Modal>
	</div>
</template>
<script>
import Common from "../common/hm.js";
export default {
  mixins: [Common], //Js
  data() {
    return {
      apimodel: "hr", //restfulapi
      type: "3", //typetype
      cateId: "5a9bf485fce9270fa4c2b6ef", //ID
      columns7: [
        {
          //
          title: "",
          key: "title"
        },
        {
          title: "",
          key: "num"
        },
        {
          title: "",
          key: "site"
        },
        {
          title: "",
          key: "telephone"
        },
        {
          title: "",
          key: "date"
        },
        {
          title: "",
          key: "action",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.show(params.index);
                    }
                  }
                },
                ""
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.remove(params.index);
                    }
                  }
                },
                ""
              )
            ]);
          }
        }
      ],
      fliter: {
        //
        data6: [],
        total: 0,
        limit: 12,
        page: 1,
        type: "3",
        title: ""
      },
      formValidate: {
        //
        title: "",
        num: "",
        site: "",
        education: "",
        detail: "",
        date: "",
        age: "",
        pay: "",
        language: "",
        telephone: "",
        sex: "",
        type: ""
      },
      ruleValidate: {
        //
        title: [
          {
            required: true,
            message: "The name cannot be empty",
            trigger: "blur"
          }
        ],
        site: [
          {
            required: true,
            message: "Please select the city",
            trigger: "change"
          }
        ],
        detail: [
          {
            required: true,
            message: "Please enter a personal detail",
            trigger: "blur"
          },
          {
            type: "string",
            min: 15,
            message: "Introduce no less than 15 words",
            trigger: "blur"
          }
        ],
        telephone: [
          {
            required: true,
            message: "Please enter a personal telephone",
            trigger: "blur"
          },
          {
            validator: this.validateMobile,
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    validateMobile(rule, value, callback) {
      //
      let reg = /^1[3|5|7|8]\d{9}$/;
      if (reg.test(value)) {
        callback(); //
      } else {
        callback(new Error("Please enter the correct phone number"));
      }
    },
    handleUpdate() {
      this.formValidate.date = new Date();
      this.$http
        .put(
          this.getTest() + "/hr/data/" + this.formValidate._id,
          this.formValidate
        )
        .then(res => {
          //this.formValidate._idthis.formValidate
          this.getData();
          this.modal6 = false;
        });
    },
    handleSubmit1() {
      this.formValidate.type = this.type;
      this.formValidate.cateId = this.cateId;
      this.formValidate.date = new Date();
      console.log(this.formValidate);
      //cateID,type
      this.$http
        .post(
          this.getTest() + "/hr/data",
          this.formValidate
        )
        .then(res => {
          this.getData();
          this.modal6 = false;
          this.formValidate.type = "";
        });
    }
  },
  created() {
    //
    this.cateid = this.$route.params.id; //id
    this.getData(); //
  }
};
</script>
<style scoped>
.content {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.content-body {
  flex: 1;
  margin-top: 20px;
}

.content-foot {
  height: 50px;
  text-align: center;
}

.message-date {
  min-height: 550px;
  display: flex;
  flex-direction: column;
  padding-bottom: 50px;
}

.message-date div:nth-of-type(1) {
  text-align: center;
  margin-top: 20px;
  height: 30px;
  font-size: 22px;
  font-weight: 600;
  text-shadow: 1px 1px 1px #999999;
}

.message-date div:nth-of-type(2) {
  text-align: left;
  text-indent: 2em;
  flex: 1;
  margin-top: 30px;
  color: black;
}

.bottom {
  width: 250px;
  margin-left: 300px;
  height: 20px;
  text-align: left;
  margin-top: 5px;
}
</style>


