export default {
    data() {
        return {
            imgUrl: 'https://www.laowaicang.com/upload/upload',//
        }
    },
    methods: {
        handleUpdate() {//
            this.formValidate.date = new Date()//  
            this.$http.put(`https://www.laowaicang.com/${this.apimodel}/data/` + this.formValidate._id, this.formValidate).then(res => {
                this.getData()
                this.modal6 = false;
            })
        },
        onSelection(rows) {
            var ids = [];
            var idLen = rows.length;
            for (var i = 0; i < idLen; i++) {
                ids.push(rows[i]._id);
            }
            this.ids = ids;
        },
        onDeletes() {
            this.$Modal.confirm({//
                title: "",
                content: "<p></p>",
                onOk: () => {
                    this.$http
                        .post(`https://www.laowaicang.com/${this.apimodel}/deletes`, {
                            ids: this.ids.toString()
                        })
                        .then(res => {
                            this.$Message.info

                                ("");
                            this.getData();
                        });
                }
            });
        },

    }
}


